* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  background: #ece7e7;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  padding-bottom: 10px;
  border: none;
  width: 150px;
  height: 200px;
  border-radius: 5px;
  background-color: white;
  margin: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card__header {
  background: #42c8f5;
  width: 100%;
  height: 20%;
  color: white;
  padding: 10px;
  text-align: center;
}

.card__title {
  font-weight: bolder;
  font-size: 1.2rem;
  color: white;
}

.card__body {
  width: 100%;
  height: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  padding-top: 15px;
}

.card__price {
  font-weight: bolder;
  font-size: 2rem;
  margin-bottom: -5px;
}

.card__description {
  color: #000000b9;
  font-size: 1.1rem;
}

.card__action {
  width: 100%;
  height: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.line {
  width: 50%;
  height: 5px;
  background: #968e8e28;
  /* margin: 4px; */
  border-radius: 5px;
  margin-bottom: -5px;
}

.card__line__0,
.card__line__2 {
  width: 60%;
}
